#nameOfStudio, #maxim {
  text-align: center;
  font-style: italic;
  animation: introFirst 5s linear;
  animation-fill-mode: forwards; }

#nameOfStudio {
  font-size: 550%;
  color: navy;
  text-shadow: 1px 1px #0000cc, 2px 2px #0000ff, 3px 3px #4040ff; }

#maxim {
  font-size: 300%;
  text-shadow: 1px 1px #666, 2px 2px #808080, 3px 3px #a0a0a0; }

@keyframes introFirst {
  0% { }
  80% {
    opacity: 1; }
  100% {
    opacity: 0; } }

#nameOfGame, #subnameOfGame {
  visibility: hidden;
  text-align: center;
  font-style: italic;
  animation: introFirst2a 10s linear;
  animation-fill-mode: forwards;
  animation-delay: 5.5s; }

#nameOfGame {
  font-size: 550%;
  margin-top: -10%;
  -webkit-text-fill-color: #800000;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black; }

#subnameOfGame {
  font-size: 300%;
  margin-top: -1%;
  -webkit-text-fill-color: #808000;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black; }

@keyframes introFirst2a {
  0% {
    opacity: 0;
    visibility: hidden; }
  20% {
    opacity: 1;
    visibility: visible; }
  80% {
    opacity: 1;
    visibility: visible; }
  100% {
    opacity: 0;
    visibility: hidden; } }

#heroBtns {
  width: 100%;
  height: 50%; }
  #heroBtns #info {
    display: flex;
    justify-content: center;
    width: 20%;
    height: 60%;
    font-weight: bold;
    color: white;
    padding: 1% 0 1% 0;
    transform: skewX(20deg);
    background-color: #a6a6a6;
    box-shadow: -1px 1px 0 #a6a6a6, -2px 2px 0 #838383, -3px 3px 0 #606060, -4px 4px 0 #606060; }
    #heroBtns #info button {
      width: 20%;
      height: 70%;
      font-weight: bold;
      color: white;
      background-color: #0000cc;
      box-shadow: -1px 1px 0 #0000cc, -2px 2px 0 #0000cc, -3px 3px 0 #0000cc, -4px 4px 0 #0000cc, -5px 5px 0 #0000cc; }
    #heroBtns #info button:active {
      animation: animButton 0.25s linear;
      animation-fill-mode: forwards; }

@keyframes animButton {
  0% {
    box-shadow: -1px 1px 0 #0000cc, -2px 2px 0 #0000cc, -3px 3px 0 #0000cc, -4px 4px 0 #0000cc, -5px 5px 0 #0000cc; }
  100% {
    box-shadow: inset 0 0 10px 0 black;
    background-color: #4040ff;
    margin-top: 6px;
    margin-left: -10px; } }
    #heroBtns #info button:focus {
      outline: none; }
  #heroBtns #licence {
    width: 24%;
    display: flex;
    justify-content: center;
    padding: 1% 0 1% 0; }
  #heroBtns #tutorial {
    width: 24%;
    display: flex;
    justify-content: center;
    padding: 1% 0 1% 0; }
  #heroBtns #game {
    width: 24%;
    display: flex;
    justify-content: center;
    padding: 1% 0 1% 0; }

