  height: 100%; }

body {
  background-color: lightgray; }

header, footer {
  width: 100%;
  height: 10vh; }

nav {
  width: 100%;
  height: 15vh; }

#alerts {
  width: 100%;
  height: 5vh; }

#mainPart {
  width: 100%;
  height: 60vh; }

header {
  text-align: center; }
  header span:first-child {
    font-size: 200%;
    font-style: italic;
    font-weight: bold;
    -webkit-text-fill-color: #800000;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black; }
  header span:last-child {
    font-size: 150%;
    font-style: italic;
    font-weight: bold;
    -webkit-text-fill-color: #808000;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black; }

#heroBtns {
  display: flex;
  justify-content: space-around; }
